import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common/index';
import { Card, Row, Col, message } from 'antd';
import { getCandidates, deleteCandidate } from 'services/studentCandidate';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchCandidate from './FormSearchCandidate';

class ListCandidateStudentPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  urlFormNew = '#/students/candidate/form/new';
  urlFormEdit = '#/students/candidate/form/edit';

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deleteCandidate({ id });
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'SBD',
        dataIndex: 'code',
        key: 'code',
      },
      {
        title: 'Họ tên',
        dataIndex: 'id',
        key: 'code',
        render: (text, record) => `${record.firstname} ${record.lastname}`,
      },
      {
        title: 'Ngày sinh',
        dataIndex: 'dob',
        key: 'dob',
      },
      {
        title: 'Giới tính',
        dataIndex: 'genderText',
        key: 'genderText',
      },
      {
        title: 'Mã trường',
        dataIndex: 'schoolId',
        key: 'schoolId',
      },
      {
        title: 'KV',
        dataIndex: 'areaId',
        key: 'areaId',
      },
      {
        title: 'Thường trú',
        dataIndex: 'permanentAddressLabel',
        key: 'permanentAddressLabel',
      },
      {
        title: 'Năm TN',
        dataIndex: 'graduatedYear',
        key: 'graduatedYear',
      },
      {
        title: 'Hệ ĐT',
        dataIndex: 'trainType',
        key: 'trainType',
      },
      {
        title: 'Mã ngành',
        dataIndex: 'studyField',
        key: 'studyField',
      },
      {
        title: 'Khối',
        dataIndex: 'testGroup',
        key: 'testGroup',
      },
      {
        title: 'Môn 1',
        dataIndex: 'gradeM1',
        key: 'gradeM1',
      },
      {
        title: 'Môn 2',
        dataIndex: 'gradeM2',
        key: 'gradeM2',
      },
      {
        title: 'Môn 3',
        dataIndex: 'gradeM3',
        key: 'gradeM3',
      },
      {
        title: 'ĐTC',
        dataIndex: 'totalGrade',
        key: 'totalGrade',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`${this.urlFormEdit}/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="">
        <CartHeadAction>
          <BtnAddNew href={this.urlFormNew} />
        </CartHeadAction>
        <FormSearchCandidate />
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getCandidates}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <ListCandidateStudentPage {...props} />
  </Page>
));



