import React, { Component } from 'react';
import { Row, Col, Form, Input } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { SelectAjax } from 'components/common/index';
import { getTrainTypes } from 'services/trainType';
import { getStudyFields } from 'services/StudyField';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'FormSearchCandidate' })
class FormSearchCandidate extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Hệ">
              {getFieldDecorator('trainTypeId')(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="CN">
              {getFieldDecorator('studyFieldId')(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  params={{ trainTypeId }}
                  service={getStudyFields}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <Form.Item label="Tên">{getFieldDecorator('fullname')(<Input allowClear />)}</Form.Item>
          </Col>
          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearchCandidate;



